<template>
    <!--  @submit.prevent="addMenuItem" -->
    <form>
        <h3 class="text-muted my-5">添加新的Pizza</h3>
        <div class="form-group row">
            <label class="col-sm-1">品种</label>
            <div class="col-sm-11">
                <input type="text" class="form-control" v-model="newPizza.name">
            </div>
        </div>
        <div class="form-group row">
            <label class="col-sm-1">描述</label>
            <div class="col-sm-11">
                <textarea rows="5" class="form-control" v-model="newPizza.description"></textarea>
            </div>
        </div>
        <p><strong>选项1</strong></p>
        <div class="form-group row">
            <label class="col-sm-1">尺寸</label>
            <div class="col-sm-11">
                <input type="number" class="form-control" v-model="newPizza.size1">
            </div>
        </div>
        <div class="form-group row">
            <label class="col-sm-1">价格</label>
            <div class="col-sm-11">
                <input type="number" class="form-control" v-model="newPizza.price1">
            </div>
        </div>
        <p><strong>选项2</strong></p>
        <div class="form-group row">
            <label class="col-sm-1">尺寸</label>
            <div class="col-sm-11">
                <input type="number" class="form-control" v-model="newPizza.size2">
            </div>
        </div>
        <div class="form-group row">
            <label class="col-sm-1">价格</label>
            <div class="col-sm-11">
                <input type="number" class="form-control" v-model="newPizza.price2">
            </div>
        </div>
        <div class="form-group row">
            <!-- 掉了一个type=button，就无法提交数据了而且页面还会刷新，是不是表单默认提交了 -->
            <!-- 如果type=submit，点击后会立刻提交表单，不会进入js代码中 -->
            <!--  @click="addMenuItem"  -->
            <button type="button" @click="addMenuItem" class="btn btn-success btn-block">添加</button>
        </div>
    </form>
</template>
<script>

export default{
    data() {
        return{
            newPizza:{}
        }
    },
    methods: {
        addMenuItem(){
            let data = {
                name:this.newPizza.name,
                description:this.newPizza.description,
                options:[
                    {
                        size:this.newPizza.size1,
                        price:this.newPizza.price1
                    },
                    {
                        size:this.newPizza.size2,
                        price:this.newPizza.price2
                    }
                ]
            }
            // 把数据添加到数据库中
            // axios,vue-resource
            // es6 fetch
            // fetch("https://pizza-app-bf56d-default-rtdb.firebaseio.com/menu.json",{
            //     method:"POST",
            //     headers: {
            //         "Content-type":'application/json',
            //     },
            //     body:JSON.stringify(data)
            // })
            //     .then(res => res.json())
            //     .then(data =>this.$router.push({name:'menuLink'}))
            //     .catch(err => console.log(err))

            // this.http.post('menu.json', data)
            //     .then(res => this.$router.push({name:'menuLink'}))

            // 数据同步到vuex中
            this.http.post('menu.json', data)
                .then(res => this.$store.commit('pushToMenuItems', data));
        }
    }
}
</script>



