<template>
  <div class="row">
    <!-- 菜单 -->
    <div class="col-sm-12 col-md-8">
      <table class="table">
        <thead class="thead-default">
          <tr>
            <th>尺寸</th>
            <th>价格</th>
            <th>加入</th>
          </tr>
        </thead>
        <tbody v-for="item in getMenuItems" :key="item.name">
          <tr>
            <td><strong>{{item.name}}</strong></td>
          </tr>
          <tr v-for="option in item.options" :key="option.size">
            <td>{{option.size}}</td>
            <td>{{option.price}}RMB</td>
            <td>
              <button 
                @click="addToBasket(item, option)"
                class="btn btn-sm btn-outline-success">+</button>
            </td>
          </tr>
        </tbody>
      </table>
    </div>

    <!-- 购物车 -->
    <div class="col-sm-12 col-md-4">
      <div v-if="baskets.length > 0">
        <table class="table">
          <thead class="thead-dafult">
            <tr>
              <th>数量</th>
              <th>品种</th>
              <th>价格</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="item in baskets" :key="item.name">
              <td>
                <button @click="decreaseQuantity(item)" class="btn btn-sm">-</button>
                <span>{{item.quantity}}</span>
                <button @click="increaseQuantity(item)" class="btn btn-sm">+</button>
              </td>
              <td>{{item.name}}{{item.size}}</td>
              <td>{{item.price * item.quantity}}RMB</td>
            </tr>
          </tbody>
        </table>
        <p>总价: {{total}}RMB</p>
        <button class="btn btn-success btn-block">提交</button>
      </div>
      <div v-else>{{basketText}}</div>
    </div>

  </div>
</template>

<script>
// import axios from 'axios'

export default {
  data(){
    return {
      baskets:[],
      basketText: '购物车没有任何商品',
      // getMenuItems:[]
      /*
      getMenuItems:{
        1: {
          'name': '榴莲pizza',
          'description': '这是喜欢吃榴莲的朋友们的最佳选择',
          'options':[{
            'size': 9,
            'price':38
          }, {
            'size':12,
            'price':46
          }]
        },
        2: {
          'name': '芝士pizza',
          'description': '芝士杀手，浓浓的芝士丝，食欲瞬间爆棚',
          'options':[{
            'size': 9,
            'price':38
          }, {
            'size':12,
            'price':48
          }]
        },
        3: {
          'name': '夏威夷pizza',
          'description': '众多人的默认选择',
          'options':[{
            'size': 9,
            'price':36
          }, {
            'size':12,
            'price':46
          }]
        }

      }*/
    }
  },
  created() {
    // fetch("https://pizza-app-bf56d-default-rtdb.firebaseio.com/menu.json")
    //   .then(res => res.json())
    //   .then(data => {
    //     // console.log(data); // 这不是一个对象吗？v-for可以遍历对象
    //     this.getMenuItems = data;
    //   })
    //   .catch(err => console.log(err));
    // axios.get('menu.json')
      // .then(res => console.log(res.data));
    
    
    // this.http.get('menu.json')
    //   .then(res=>this.getMenuItems = res.data);

    // 把请求下来的数据存储到vuex中
    this.http.get('menu.json')
      .then(res=>this.$store.commit('setMenuItems', (res.data)));

  },
  methods: {
    addToBasket(item, option) {
      let basket = {
        name:item.name,
        size:option.size,
        price:option.price,
        quantity:1
      };
      if (this.baskets.length > 0) {
        // 过滤
        let result = this.baskets.filter(item => {
          return item.name == basket.name && item.size == basket.size;
        });
        if (result != null && result.length > 0) {
          result[0].quantity++;
        } else {
          this.baskets.push(basket);
        }
      } else {
        this.baskets.push(basket);
      }
    },
    // 为什么直接改item就可以修改整个数组?
    // 是因为key,还是因为是浅拷贝???
    decreaseQuantity(item) {
      item.quantity--;
      if (item.quantity <= 0) {
        this.removeFromBasket(item);
      }
    },
    increaseQuantity(item) {
      item.quantity++;
    },
    removeFromBasket(item){
      this.baskets.splice(this.baskets.indexOf(item), 1);
    }
  },
  computed: {
    total: function() {
      let totalCost = 0;
      for (let index in this.baskets) {
        let individualItem = this.baskets[index];
        totalCost += individualItem.quantity * individualItem.price;
      }
      return totalCost;
    },
    getMenuItems() {
      // 在vuex中获取数组
      // return this.$store.state.menuItems;
      return this.$store.getters.getMenuItems
    }
  }
}
</script>



