<template>
  <div class="row mt-3">
    <div class="col-md-12 col-lg-12">
      <div class="card">
        <div class="card-body">
          <img class="max-atuo d-block" src="../assets/register.jpg" alt="">
          <form @submit.prevent="onSubmit">
            <div class="form-group">
              <label for="email">邮箱</label>
              <input
                type="email"
                class="form-control"
                v-model="email"
                required
              >
            </div>
            <div class="form-group">
              <label for="password">密码</label>
              <input
                type="password"
                class="form-control"
                v-model="password"
                required
              >
            </div>
            <div class="form-group">
              <label for="confirmPassword">确认密码</label>
              <input
                type="password"
                class="form-control"
                v-model="confirmPassword"
                required
              >
            </div>
            <button class="btn btn-block btn-success">注册</button>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  data() {
    return {
      email:'',
      password:'',
      confirmPassword:''
    }
  },
  methods:{
    onSubmit(){
      if (this.password === this.confirmPassword) {
        const formData = {
          email: this.email,
          password:this.password
        }
        axios.post('/users.json', formData)
          .then(res=>{
            this.$router.push({name:'loginLink'})
          })
      } else {
        alert("两次密码不一致！");
      }
    }
  }
}
</script>



