<template>
  <div class="row">
    <div class="co-sm-12 col-md-8">
      <!-- new pizza -->
      <app-new-pizza></app-new-pizza>
    </div>
    <div class="col-sm-12 col-md-4">
      <!-- 品种展示 -->
      <h3 class="text-muted my-5">菜单</h3>
      <table class="table">
        <thead class="table table-default">
          <tr>
            <th>品种</th>
            <th>删除</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="item in getMenuItems" :key="item.id">
            <td>{{item.name}}</td>
            <td>
              <button @click="deleteData(item)" class="btn btn-outline-danger btn-sm">&times;</button>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
import newPizza from './newPizza.vue'
export default {
  components: {
    'app-new-pizza':newPizza
  },
  data() {
    return {
      // name:"Henry"
    }
  },
  created(){
    fetch("https://pizza-app-bf56d-default-rtdb.firebaseio.com/menu.json")
      .then(res => res.json())
      .then(data => {
        let menuArray = [];
        for (let key in data) {
          data[key].id = key;
          menuArray.push(data[key]);
        }
        // this.getMenuItems = menuArray
        // 数据同步
        this.$store.commit('setMenuItems', menuArray)
      })
  },
  methods: {
    deleteData(item){
      // fetch("https://pizza-app-bf56d-default-rtdb.firebaseio.com/menu/" + item.id+ "/.json", {
      //   method:"DELETE",
      //   headers: {
      //     'Content-type':'application/json'
      //   }
      // })
      //   .then(res => res.json())
      //   .then(data => this.$router.push({name:'menuLink'}))
      //   .catch(err => console.log(err))
      this.http.delete('menu/' + item.id + '/.json')
        // .then(res => this.$router.push({name:'menuLink'}))
        .then(res => {
          this.$store.commit('removeMenuItems', item);
        })
    }
  },
  computed: {
    // getMenuItems: {
    //   // 在vuex中获取数据
    //   get() {
    //     return this.$store.state.menuItems;
    //   },
    //   set(){

    //   }
    // }
    getMenuItems() {
      // 在vuex中获取数组
      // return this.$store.state.menuItems;
      return this.$store.getters.getMenuItems;
    }
  }
  // 组件内守卫
  // beforeRouteEnter:(to, from, next)=>{
  //   // alert("Hello " + this.name);
  //   // next();
  //   next(vm => {
  //     alert("hello " + vm.name);
  //   })
  // },
  // beforeRouteLeave(to, from, next) {
  //   if (confirm("确认离开吗？") === true) {
  //     next();
  //   } else {
  //     next(false);
  //   }
  // }
}
</script>



