const state = {
    currentUser:{},
    isLogin: false
}

const getters = {
    getCurrentUser:state => {
        console.log('getCurrentUser function')
        return state.currentUser
    },
    getIsLogin: state => {
        console.log("getIsLogin function");
        return state.isLogin;
    }
}

const mutations = {
    userStatus(state, user){
        console.log(state.currentUser, state.isLogin)
        if (user !== null) {
            state.currentUser = user;
            state.isLogin = true;
        } else {
            state.currentUser = null;
            state.isLogin = false
        }
        console.log(state.currentUser, state.isLogin)
    }
}

const actions = {
    setUser ({commit}, user) {
        commit('userStatus', user);
    }
}

export default{
    state,
    getters,
    mutations,
    actions
}


